.venom body,
.venom button,
.venom input,
.venom select,
.venom textarea,
.venom span,
.venom small,
.venom strong,
.venom i {
  box-sizing: border-box !important;
  color: currentColor;
}

.venom article,
.venom aside,
.venom figure,
.venom footer,
.venom header,
.venom hgroup,
.venom section {
  box-sizing: border-box !important;
  display: block;
}

.venom div {
  box-sizing: border-box !important;
}

.venom code,
.venom pre {
  -moz-osx-font-smoothing: auto;
  -webkit-font-smoothing: auto;
  font-family: monospace;
}

.venom body {
  background-color: #fafafa;
  color: #333;
  font-family: BlinkMacSystemFont, -apple-system, "Open Sans", sans-serif;
  font-weight: 400;
  letter-spacing: 0.25px;
  font-size: 14px;
}

.venom a {
  box-sizing: border-box !important;
  color: #00549e;
  cursor: pointer;
  text-decoration: none;
}

.venom a strong {
  color: currentColor;
}

.venom a strong:hover {
  color: #004785;
}

.venom a strong:active {
  color: #2e77b2;
}

.venom a strong:visited {
  color: #004785;
}

.venom hr {
  border-width: 0;
  display: block;
  height: 1px;
  width: 100%;
}

.venom hr::after {
  clear: both;
  content: " ";
  display: table;
}

.venom img {
  height: auto;
  width: 100%;
}

.venom input[type='checkbox'],
.venom input[type='radio'] {
  vertical-align: baseline;
}

.venom button,
.venom textarea,
.venom select,
.venom input {
  box-sizing: border-box !important;
  outline: none;
}

.venom small {
  font-size: 12px;
  font-weight: inherit;
}

.venom span {
  font-style: inherit;
  font-weight: inherit;
}

.venom strong {
  font-weight: 700;
}

.venom fieldset {
  border-width: 0;
}

.venom h1,
.venom h2,
.venom h3,
.venom h4,
.venom h5,
.venom h6,
.venom p {
  box-sizing: border-box !important;
  color: currentColor;
  line-height: 1.25;
  margin: 0;
  padding: 0;
  max-width: 100%;
}

.venom h1::after,
.venom h2::after,
.venom h3::after,
.venom h4::after,
.venom h5::after,
.venom h6::after,
.venom p::after {
  clear: both;
  content: " ";
  display: table;
}

.venom p + p {
  margin-bottom: 20px;
}

.venom h1 {
  font-size: 32px;
  font-weight: 300;
  letter-spacing: 0.25px;
}

.venom h2 {
  font-size: 28px;
  font-weight: 300;
  letter-spacing: 0.18px;
}

.venom h3 {
  font-size: 24px;
  font-weight: 300;
}

.venom h4 {
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 0.25px;
}

.venom h5 {
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.venom h6 {
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0.25px;
}

.venom h1 + h2,
.venom h2 + h3,
.venom h2 + p,
.venom h3 + p {
  margin-top: 12px;
}

.venom p {
  letter-spacing: 0.25px;
}

.venom pre {
  overflow-x: auto;
  white-space: pre;
  word-wrap: normal;
}

.venom pre::after {
  clear: both;
  content: " ";
  display: table;
}

.venom pre code {
  background-color: transparent;
  color: currentColor;
  padding: 0;
}

.venom table td,
.venom table th {
  vertical-align: top;
}

.venom table td:not([align]),
.venom table th:not([align]) {
  text-align: left;
}

